@import '../common/styles.scss';
table {
    width: 100%;
    border-collapse: collapse;
    position: relative;
  }
  
  thead tr th {
    font-weight: 500;
    text-align: left;
    background-color: $Blue;
  }
  
  tbody tr:nth-child(even) {
    background-color: #fafafa;
  }
  
  tbody tr{
    background-color: $Grey;
    text-align: left;
  }
  
  tbody tr:hover{
    background-color: $Yellow;
  }
  
  th,
  td {
    padding: 8px;
    overflow-wrap: break-word;
  }